* {
  padding: 0;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}
.App {
  height: 100%;
  width: 100%;
}

.banner__title {
  color: white;
  font-size: 2rem;
  letter-spacing: 3.5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 93vh;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.7)),
    url("./Images/truck.jpg") center no-repeat;
  background-size: cover;
}

.banner__title > h1 {
  animation-delay: 1s;
  margin-bottom: 2%;
  animation-duration: 2.5s;
}
.banner__title > p {
  font-size: 1.2rem;
}

.banner__title > button {
  color: #39393a;
  padding: 10px;
  height: 65px;
  width: 200px;
  margin-top: 7%;
  font-size: 22px;
  cursor: pointer;
}

/* Subheadline */

.sub__headline-container {
  width: 100%;
  margin: 5% 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.sub__headline-container > h3 {
  color: #39393a;
  font-size: 2.5rem;
}
.sub__headline-container::after {
  content: "";
  width: 0.3rem;
  height: 3em;
  background-color: grey;
  transform: rotate(90deg);
  position: absolute;
}
.sub__headline-container > p {
  color: #39393a;
  font-size: 1.2rem;
  margin-top: 2%;
}

.sub__headline-box {
  margin-top: 7%;
  height: 10vh;
  width: 20%;
  font-size: 1.5rem;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgb(0, 45, 114, 0.9);
  border: 1px solid #39393a;

  cursor: pointer;
  transition: ease-in-out 1s;
}
.sub__headline-box:hover {
  width: 99%;
  font-size: 2.5rem;
  height: 15vh;
  border-top: 4px solid #39393a;
  border-bottom: 4px solid #39393a;
}
/* Scrolled Contact Button */

.scrolled__contact-button {
  height: 3em;
  width: 6.5em;
  font-weight: 600;
  cursor: pointer;
  color: #002d72;
  font-size: 1.1rem;
  background-color: rgb(255, 255, 255, 0.7);
  border: 3px solid #002d72;
  position: fixed;
  right: 2.5%;
  top: 30%;
  z-index: 10000;
}

