.section__one-title {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  height: 100%;
  width: 100%;
  color: white;
  background-color: rgb(0, 45, 114, 0.9);
}
.section__one-title h1 {
  font-size: 60px;
  text-shadow: 4px 4px 2px rgba(150, 150, 150, 1);
  border-bottom: 2px solid white;
}

.section__one-title > p {
  line-height: 3.6rem;
  font-size: 24px;
  letter-spacing: 1.5px;
  width: 50%;
  color: white;
}

/* Section 2 */
.section__two {
  object-fit: contain;
  height: 70vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin: 10% 0;
}
.section__two > img {
  width: 50%;
  height: 100%;
  border-radius: 25px;
  -webkit-box-shadow: 5px 5px 15px -2px #000000;
  box-shadow: 5px 5px 15px -2px #000000;
}

.section__two-title {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  height: 100%;
  width: 40%;
  color: #002d72;
}
.section__two-title > p {
  line-height: 3.6rem;
  font-size: 24px;
  letter-spacing: 1px;
  width: 100%;
  color: #414141;
}
.section__two-title > h1 {
  line-height: 42px;
  font-size: 36px;
  text-shadow: 4px 4px 2px rgba(150, 150, 150, 1);
}
