}

.section__three-title {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  height: 100%;
  width: 100%;
  color: white;
  background-color: rgb(0, 45, 114, 0.9);
}
.section__three-title h1 {
  font-size: 60px;
  text-shadow: 4px 4px 2px rgba(150, 150, 150, 1);
  border-bottom: 2px solid white;
}

.section__three-title > p {
  line-height: 3.6rem;
  font-size: 24px;
  letter-spacing: 1px;
  width: 50%;
  color: white;
}
.section__three-title > p > span {
  font-weight: 700;
  font-size: 1.5rem;
}

/* Section 4 */

.section__four {
  height: 100%;
  margin: 7% 0;
  display: grid;
  align-items: center;
  justify-content: center;
}

.section__four-title > p {
  color: #002d72;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  font-weight: 600;
  margin-top: 7%;
}

.section__four-questions {
  border: 4px solid #002d72;
  color: #39393a;
  font-size: 36px;
  height: 40%;
  padding: 10px 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  letter-spacing: 2.5px;
}

.section__four-title {
  width: 100%;
}
.section__four-img {
  margin-top: 10%;
  display: flex;
  justify-content: center;
}

.section__four-img > img {
  width: 350px;
}

/* media quieries */
@media only screen and (max-width: 1400px) {
  .section__three-title,
  .section__one-title {
    display: grid;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  .section__three-title > p,
  .section__one-title > p {
    width: 100%;
    font-size: 32px;
  }
  .section__three {
    display: grid;
  }
}
@media only screen and (max-width: 1200px) {
  .section__two > img {
    display: none;
  }
  .section__two-title {
    width: 100%;
    text-align: center;
  }
  .section__two-title > p {
    font-size: 32px;
  }
  .section__two-title > h1 {
    font-size: 52px;
  }
}
@media only screen and (max-width: 900px) {
  .sub__headline-box {
    width: 100%;
    background-color: rgb(216, 216, 216);
    color: #002d72;
  }
}

